* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: sans-serif;
	font-size: 14px;
}

body {
	background: url("https://initiate.alphacoders.com/images/421/cropped-1920-1080-421124.jpg?6935");
}

.hide {
	display: none;
}

ul {
	list-style-type: none;
	padding: 0;
}

.search-bar-component {
	border: 1px solid #ADADAD;
	border-radius: 5px;
	padding: 0;
	background: #fff;
}

.search-dropdown {
	width: 100%;
}

.search-bar {
	width: 100%;
	background: #fff;
	border-top-left-radius: 5px;
	border-top-right-radius: 5px;
}

.close {
	position: fixed;
	right: 7%;
	/*top: 1%;*/
}

.close:hover {
	cursor: pointer;
}

.search-bar-input {
	display: flex;
	justify-content: space-between;
	align-items: center;
	overflow: hidden;
	border-radius: 8px;

}

.search-bar-input label {
	/*padding: 10px;*/
	width: 20%;
	text-align: center;
	color: #6B6B6B;
}

.search-bar-input label:hover{
	cursor: pointer;

}

.search-results li {
	padding: 10px;
	position: relative;
	width: 100%;

	border-bottom: 1px solid #ADADAD;
}

.search-results li:first-child {
	border-top: 1px solid #ADADAD;
}

.search-results li:last-child {
	border-bottom: none;
}

.search-results li:hover {
	cursor: pointer;
	background: #D0D0D0;
}

.search-results li {
	text-decoration: none;
	color: #000;
	display: flex;
}

.search-results img {
	margin-right: 20px;
	width: 84px;
	height: 115px;
}

.book-title {
	margin-bottom: 3px;
}

#btn {
	display: none;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active  {
    -webkit-box-shadow: 0 0 0 30px white inset !important;
    border-radius: 5px;
}

input[name="q"] {
	padding: 10px 5px;
	outline: none;
	border: none;
	width: 80%;
}

input[name="q"]:active {
	background: #fff;
}

.search-bar-submit {
	margin-left: 10px;
	padding: 10px 5px;
	outline: none;
	border: 1px solid #ADADAD;
	border-radius: 5px;
}

.search-bar-submit:hover {
	cursor: pointer;
}

.search-results img {
	border: 1px solid #E4E4E4;
}

.search-component {
	display: flex;
	justify-content: center;
}

.modal-block {
	position: fixed;
	width: 100vw;
	height: 100vh;
	background: rgba(0,0,0,0.8);
	top: 0;
	left: 0;
	padding: 5% 5%;
	overflow-x: hidden;
	overflow-y: auto;
	transition: all 1s ease 0s;
	opacity: 0;
	transform: perspective(600px) translate(0px, -100%) rotateX(45deg);
	z-index: 11;
}

.modal-wrap {
	min-height: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: flex-start;
	padding: 50px;
	background: #fff;
	border-radius: 8px;
	transition: all 1s ease 0s;
	transform: perspective(600px);
}

.active {
	opacity: 1;
	transform: perspective(600px) translate(0px, 0%) rotateX(0deg);
}

.hide {
	display: none;
}

.block {
	display: block;
}

.modal-img img {
	border: 1px solid #C0C0C0;
	border-radius: 8px;
	overflow: hidden;
	height: 200px;
	width: 150px;
	margin: 0 auto;
}

.modal-img {
	max-width: 253px;
	max-height: 323px;
	min-width: 150px;
	min-height: 200px;

}

.modal-img img {
	width: 80%;
	height: 100%;
}

.sm-mb {
	margin-bottom: 30px;
}

.modal-desc {
	line-height: 20px;
}

.modal-desc div {
	margin-bottom: 20px;
}

.modal-desc span {
	font-size: 18px;
	font-weight: bold;
	
}

